@import './../../../scss/global';

.wrapper {
    height: 90vh;
    padding: 5% 10%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    background-color: $color-third;
    align-items: center;
    @include media-query('lap') {
        height: 100vh;
    }
    @include media-query('max', $palm-start) {
        height: 80vh;
    }
    @include media-query('max', $mob-start) {
        height: 90vh;
    }
    .video {
        width: 45%;
        @include media-query('max', $wide-start) {
            margin: 0 auto;
            width: 90%;
            height: auto;
        }
        @include media-query('max', $lap-start) {
            margin: 0 auto;
            width: 100%;
            height: auto;
        }
        &:focus {
            outline: none;
        }
    }
    .textWrapper {
        padding: 2rem;
        width: 45%;

        @include media-query('max', $desk-start) {
            width: 100%;
            padding: 1rem 0;
        }
        @include media-query('max', $wide-start) {
            width: 100%;
            padding: 1rem 0;
        }
        .title {
            color: $color-first;
            margin-bottom: 7px;
            position: relative;
        }

        .subtitle {
            font-size: 1.5rem;
            line-height: 32px;
            @include media-query('max', $lap-start) {
                font-size: 1.2rem;
            }
            @include media-query('max', $palm-start) {
                font-size: 1rem;
            }
        }
    }
}
