@import './../../../scss/global';

.wrapper {
    height: 80vh;
    width: 100%;
    padding: 5% 10%;
    background: linear-gradient(180deg, #428da0 0%, #2fdd92 100%);
    @include media-query('max', $page-width) {
        height: auto;
    }
    @include media-query('max', $desk-start) {
        height: 100vh;
    }
    @include media-query('tab') {
        height: auto;
        padding: 15% 10%;
    }

    .title {
        text-align: center;
        margin-bottom: 7px;
        color: $color-light;
    }
    .prodWrapper {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}
