@import './../../../scss/global';
.wrapper {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1000;
    .logo {
        width: 2rem;
        height: 3rem;

        @include media-query('max', $lap-start) {
            width: 1.5rem;
            height: 2.2rem;
        }
    }
    .text {
        margin-left: 5px;
        color: #fff;
        font-size: 1.2rem;
        font-weight: 800;
    }
}
