@import './../../../scss/global';
.wrapper {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 5%;
    min-height: 8vh;

    @include media-query('tab') {
        background-color: $color-first;
        z-index: 2000;
        position: fixed;
    }
}
.active {
    transform: translateX(0%);
}
