@import './../../../scss/global';

.textarea {
    width: 100%;
    height: 150px;
    padding: 10px 8px;
    font-size: 1rem;
    border: 2px solid $color-second;
    outline: none;
    resize: none;
    background: transparent;
    &::placeholder {
        color: $color-second;
    }
    @include media-query('max', $palm-start) {
        font-size: 14px;
    }
}
.textarea:focus {
    background: #b6d8c9;
    border: transparent;
    box-shadow: 0 0 0 2px $color-second;
}
.errorTextarea {
    width: 100%;
    padding: 10px 8px;
    font-size: 1rem;
    border: transparent;
    box-shadow: 0 0 0 3px $color-danger;
    outline: none;
    resize: none;
}

.error {
    color: $color-danger;
    font-weight: 400;
    font-size: 1rem;
}
