@import './../../../scss/global';

.wrapper {
    width: 100%;

    .header {
        font-size: 2.7rem;
        font-weight: 600;

        @include media-query('max', $desk-start) {
            font-size: 2.2rem;
        }
        @include media-query('max', $palm-start) {
            font-size: 1.7rem;
        }
    }
}
