@import './../../../scss/global';

.wrapper {
    width: 45%;
    margin: 0 auto;
    padding: 2rem;
    background: #f6f0e8;
    -webkit-box-shadow: 2px 3px 11px -4px #545454;
    box-shadow: 2px 3px 11px -4px #545454;
    border-radius: 5px;
    @include media-query('max', $lap-start) {
        width: 100%;
    }
    @include media-query('lap') {
        width: 70%;
    }
    .input {
        margin-bottom: 1rem;
    }
    .btn {
        width: 100%;
        margin-top: 1rem;
    }
}
