@import './../../../scss/global';

.burger div {
    width: 25px;
    height: 3px;
    margin: 5px;
    background-color: #fff;
}
.burger {
    display: none;
    cursor: pointer;
    z-index: 2000;
    position: relative;
    @include media-query('tab') {
        display: block;
    }
}
.toggle {
    .line1 {
        transform: rotate(-45deg) translate(-5px, 6px);
    }
    .line2 {
        opacity: 0;
    }
    .line3 {
        transform: rotate(45deg) translate(-5px, -6px);
    }
}
