@import './../../../scss/global';

.wrapperMain {
    height: 92vh;
    padding: 0 10%;
    @include media-query('tab') {
        height: 100vh;
    }
    .wrapper {
        padding: 17rem 0 0 0;
        top: 12rem;
        position: absolute;

        @include media-query('max', $palm-start) {
            padding: 7rem 0 0 0;
        }
    }
    .video {
        position: absolute;
        right: 0;
        bottom: 0;
        min-height: 100vh;
        width: 100%;
        object-fit: cover;
        opacity: 0.5;
        filter: grayscale(100%);
    }
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background: #186e79;
        mix-blend-mode: overlay;
    }
    .title {
        font-size: 3.4rem;
        color: #fff;

        @include media-query('max', $desk-start) {
            font-size: 2.6rem;
        }
        @include media-query('max', $palm-start) {
            font-size: 2rem;
        }
    }

    .subtitle {
        color: #fff;
        @include media-query('max', $lap-start) {
            font-size: 1.4rem;
        }
        @include media-query('max', $palm-start) {
            font-size: 1.2rem;
        }
    }
    .subtitleWrapper {
        width: 80%;
        margin-top: 0.5rem;
    }
    .titleWrapper {
        @include media-query('tab') {
            width: 80%;
        }
    }
}
