@import './../../../scss/global';
.wrapper {
    width: 100%;

    .imgWrapper {
        margin: auto;
        position: relative;
        overflow: hidden;

        .img {
            max-width: 100%;
            max-height: 100%;
        }
        .playBtn {
            width: 65px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            cursor: pointer;
            opacity: 0.6;
        }
    }
    .player {
        width: 70%;
        position: absolute;
        top: 145vh;
        left: 50%;
        transform: translate(-50%, -50%);

        .video {
            max-width: 100%;
            max-height: 100%;
            outline: none;
            &:focus {
                outline: none;
            }
        }
        @include media-query('max', $wide-start) {
            width: 90%;
            left: 50%;
        }

        @include media-query('max', $desk-start) {
            width: 90%;
            left: 50%;
            top: 167vh;
        }
    }
    .closeBtn {
        width: 45px;
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
        opacity: 0.7;
    }
}
