@import './../../../scss/global';

.wrapper {
    width: 44%;
    height: 430px;
    background-color: #e8e1d978;
    -webkit-box-shadow: 2px 3px 11px -4px #7e7e7e;
    box-shadow: 2px 3px 11px -4px #757575;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 3.5rem;
    margin-right: 4rem;
    cursor: pointer;
    transition: all 0.5s;
    border-radius: 5px;
    @include media-query('max', $page-width) {
        width: 80%;
        margin: 0 auto;
        margin-top: 3rem;
    }
    @include media-query('max', $wide-start) {
        width: 100%;
    }
    @include media-query('max', $desk-start) {
        height: auto;
    }
    @include media-query('tab') {
        flex-direction: column;
        align-items: center;
        padding: 10px;
        justify-content: start;
    }
    &:hover {
        -webkit-transform: translateY(-20px);
        -ms-transform: translateY(-20px);
        transform: translateY(-10px);
        box-shadow: 0 22px 43px rgba(0, 0, 0, 0.109);
    }

    .textWrapper {
        margin-left: 1rem;

        .descr {
            font-size: 1rem;
            font-weight: 500;
            @include media-query('max', $page-width) {
                font-size: 1.1rem;
            }
            @include media-query('max', $desk-start) {
                font-size: 1rem;
            }
            @include media-query('tab') {
                font-size: 14px;
                margin-left: 0;
            }
        }
    }
    .img {
        width: 50%;
        height: 80%;
        @include media-query('max', $page-width) {
            width: 60%;
        }
        @include media-query('max', $desk-start) {
            width: 40%;
        }
        @include media-query('tab') {
            margin-bottom: 10px;
            width: 170px;
        }
    }
}
